import { getTopAnime, getPopularAnime } from '@/lib/api'
import { Navbar } from '@/components/navbar'
import { Suspense } from 'react'
import { SkeletonGrid } from '@/components/skeleton-loader'
import { AnimeCard } from '@/components/anime-card'
import { HeroCarousel } from '@/components/hero-carousel'

async function HeroSection() {
  const data = await getTopAnime(1)
  const animes = data?.data ? data.data.slice(0, 5) : []
  return (
    <div className="mb-12 max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
      {/* HeroCarousel cinematic, height lebih besar */}
      <HeroCarousel animes={animes} height="h-[20rem] md:h-[35rem]" />
    </div>
  )
}

async function TopSection() {
  const data = await getTopAnime(1)
  const animes = data?.data ? data.data.slice(0, 5) : []
  return (
    <div className="mb-12 max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
      <h2 className="text-2xl font-bold text-foreground mb-4">Top Anime</h2>
      <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-5 gap-4 md:gap-6">
        {animes.map(anime => <AnimeCard key={anime.mal_id} anime={anime} />)}
      </div>
    </div>
  )
}

async function PopularSection() {
  const data = await getPopularAnime(1)
  const animes = data?.data || []
  return (
    <div className="mb-12 max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
      <h2 className="text-2xl font-bold text-foreground mb-4">Popular Anime</h2>
      <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-4 md:gap-6">
        {animes.map(anime => <AnimeCard key={anime.mal_id} anime={anime} />)}
      </div>
    </div>
  )
}

async function TrendingSection() {
  const data = await getTopAnime(1)
  const animes = data?.data ? data.data.slice(5, 25) : []
  return (
    <div className="mb-12 max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
      <h2 className="text-2xl font-bold text-foreground mb-4">Trending This Week</h2>
      <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-4 md:gap-6">
        {animes.map(anime => <AnimeCard key={anime.mal_id} anime={anime} />)}
      </div>
    </div>
  )
}

